// 카테고리별 포스트
export const popularPosts = allPosts.filter(
  (post) => post.category === "popular"
);
export const todayDeadline = allPosts.filter(
  (post) => post.category === "deadline"
);
export const paidSeries = allPosts.filter((post) => post.category === "paid");

// ID로 포스트 찾기
export function getPostById(id) {
  return allPosts.find((post) => post.id === id);
}
